<template>
  <div id="getPage">
    <h1>User Information</h1>
		<button @click="reGetData1"  class="bt">GetData from PHP</button><br/>
		<input v-model="getOutData"  class="textBox" type="text">
  <p v-if="getData[0]">Response: {{ getData[0] }}
		<ul>
      <!-- 배열의 각 요소(item)와 인덱스(index)를 사용 -->
      <li v-for="(item, index) in getData" :key="index">
        {{ index }}: {{ item }}
      </li>
    </ul>
	</p>
		
    <div v-else>
      <p>실패</p>
    </div>

		<button @click="rePostData1" class="bt">PostData from PHP</button><br/>
		<input v-model="postOutData" class="textBox" type="text">
		<p v-if="postData[0]">Response: {{ postData[0] }}
			<ul>
				<!-- 배열의 각 요소(item)와 인덱스(index)를 사용 -->
				<li v-for="(item, index) in postData" :key="index">
					{{ index }}: {{ item }}
				</li>
			</ul>
		</p>
			
			<div v-else>
				<p>실패</p>
			</div>

  </div>
	
</template>

<script>

export default {
  name: 'getPage',
  created() {
		this.reGetData1();
		this.rePostData1();
	},
	data(){
		return{
			getData : ["","","",""],
			getOutData : "get",
			postData : ["","","",""],
			postOutData : "post"
		}
	},
	methods: {
		reGetData1(){
			console.log("reGetData1 in")
      this.$networkManager.getData(this.getOutData)
          .then(({data}) => {
						console.log("reGetData1 in2")
            console.log(data)
            this.getData[0] = data.Redata1
            this.getData[1] = data.Redata1
						//this.$set(this.getData, 1, data.Redata2+"(set)"); //ver3에서 사용X
						console.log( this.getData)
          })
    },
		rePostData1(){
			console.log("repostData1 in")
      this.$networkManager.postData(this.postOutData)
          .then(({data}) => {
						console.log("repostData1 in2")
            console.log(data)
            this.postData[0] = data.Redata1
						//this.$set(this.postData, 1, data.Redata2+"(set)"); //ver3에서 사용X
						console.log( this.postData)
          })
    },
	}
}
</script>

<style scoped>
	.bt{
		background-color: gray;
		border: 1px solid black;
		padding: 5px;
	}
	.textBox{

		border: 1px solid black;
		padding: 5px;
	}
/* 스타일링은 필요에 따라 추가 */
</style>
